.signin-navbar{
    background-color: white;
    width: 100%;
    height: 80px;
}
.sigin-page-logo{
    width: 130px;
    padding: 15px;
}
.signin-div{
    background-color:#f5f5f5;

}
.form-div{
    display: flex;
    justify-content: center;
    align-items: center;
}
.form{
background-color: white;
width: 450px;
box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
border: 2px solid #fcfdff;
}
.form label{
    color: black;
    font-weight: 400;
}
.label-div{
    display: flex;
    justify-content: space-between;
}
.label-div a{
    color: blue;
    text-decoration: underline;
}
.passwrd-div{
    margin-top: 30px;
}
.input{
 margin-top: 15px;
}
.sub-btn{
    width: 420px;
    margin-top: 30px;
    border-radius: 5px ;
    padding: 10px;
}
.hr{
    display: flex;
    flex-direction: row;
    padding-top: 20px;
    color: #577592;
}
  
.hr:before,
.hr:after {
    content: "";
    flex: 1 1;
    border-bottom: 1px solid grey;
    margin: auto;
}
.login-with-google-btn {
    transition: background-color .3s, box-shadow .3s;
      
    padding: 12px 16px 12px 42px;
    border: none;
    border-radius: 3px;
    box-shadow: 0 -1px 0 rgba(0, 0, 0, .04), 0 1px 1px rgba(0, 0, 0, .25);
    
    color: #757575;
    font-size: 14px;
    font-weight: 500;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
    
    background-image: url(data:image/svg+xml;base64,);
    background-color: white;
    background-repeat: no-repeat;
    background-position: 12px 11px;
  }
  
  .icon-btn{
   display: flex;
   justify-content: center;
   align-items: center;
  }
  .anker-div{
color: BLUE;
text-align: center;
text-decoration: underline;
padding-top: 20px;
  }

#signup-link{
    color: blue;
    text-decoration: underline;
    padding-left: 5px;
}